pre {
  font: 0.9rem monospace;
  width: 70%;
  max-width: 90%;
  padding: 1em;
  margin: 2em 0.5em;
  overflow: auto;
  background-color: #EFF0F2;
  border-left: 3px solid darkgrey;
}

:not(pre) > code {
  padding: 0.2em;
  font-size: 0.9rem;
  background-color: #EFF0F2;
}

textarea {
  margin: 0;
  padding: 0.3rem;
  font-size: 1.2rem;
  overflow: auto;
  resize:vertical;
  width: 50%;
  height: 12rem;
  box-sizing: border-box;
}